	module.exports = {"data":{"post":{"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/01/crh0113.jpg\" height=\"300\" width=\"400\" border=\"0\" alt=\"crh0113.jpg: \" /></p>\n<p>After taking several days off after returning from the hospital, Mama is again writing for the <a href=\"http://www.mausnews.de\">Maus Newsletter</a>, mostly in German. Today she started her childbirth report.</p>\n<p>It's hard to hold a baby and type at the computer simultaneously. Is there such a thing as a keyboard that can be used with one hand?</p>\n<p>Oma is here for her first visit of Christopher. We have a couple of pictures in the Week 3 album at <a href=\"http://albums.photopoint.com/j/AlbumList?u=185392\">Photopoint</a></p>","fields":{"title":"This child is supposed to be sleeping","date":"13 Jan 2000","url":"https://papascott.de/2000-01-13-this-child-is-supposed-to-be-sleeping/"}}},"pathContext":{"slug":"/2000-01-13-this-child-is-supposed-to-be-sleeping/","prev":{"fields":{"title":"Our little hippo","date":"12 Jan 2000","slug":"/2000-01-12-our-little-hippo/"}},"next":{"fields":{"title":"Grandma Ruth","date":"14 Jan 2000","slug":"/2000-01-14-grandma-ruth/"}}}}

